import Backendless from '../Backendless';

function Subscriptions(args) {
  args = args || {};

  this.ownerId = args.ownerId || null;
  this.created = args.created || null;
  this.objectId = args.objectId || null;
  this.updated = args.updated || null;
  this.email = args.email || null;
  this.event_id = args.event_id || null;
  this.event = args.event || null;

  this.___class = "Subscriptions";
}

function SubscriptionClass() {
  const subscriptionCollection = Backendless.Persistence.of(Subscriptions);

  function saveSubscription(data){
    const subscriptionObject = new Subscriptions(data);
    return subscriptionCollection.save(subscriptionObject);
  }

  this.saveSubscription = saveSubscription;
}

export default SubscriptionClass;



