<template>
 <div>{{categoryName}}</div>
</template>

<script>
  import _ from 'lodash';

  var eventTitles = {
    'treningssamlinger': 'Trenings\u00ADsamlinger',
    "pressekonferanser":'Presse\u00ADkonferanser',
    'uci kongress':'Uci\u00AD kongress'
  };

  function eventHyhenator(name) {
    return eventTitles[name.toLowerCase()] || name;
  }

  export default {
    props: ['category_id'],
    computed: {
      category: function(){
        return _.find(this.$store.state.categories, { 'objectId': this.category_id});
      },
      categoryName: function(){
        return this.category ? eventHyhenator(_.get(this.category, 'name', false)) : '';
      }
    }
  }
</script>



