<template>
  <form class="filter-list" action="">
    <event-filter-item
      v-bind:values="filterOrganizations"
      v-bind:callback="dispatchFilterBy('switchFilterByOrganization')"
      v-bind:value="filterOrganizationValue">Organisasjoner</event-filter-item>
    <event-filter-item
      v-bind:values="filterLevels"
      v-bind:callback="dispatchFilterBy('switchFilterByLevel')"
      v-bind:value="filterLevelValue">Nivå</event-filter-item>
    <event-filter-item
      v-bind:values="filterRegions"
      v-bind:callback="dispatchFilterBy('switchFilterByRegion')"
      v-bind:value="filterRegionValue">Regions</event-filter-item>
    <event-filter-item
      v-for="category in filterCategories"
      v-bind:values="category.options"
      v-bind:callback="dispatchFilterBy('switchFilterByCategory')"
      v-bind:value="filterCategoryValue">{{category.name}}</event-filter-item>
    <!-- button>Apply</button -->
  </form>
</template>

<script>
  import _ from 'lodash';
  import EventFilterItem from './EventFilterItem';

  function getOptions(data) {
    return _.map(data, (v => _.pick(v, ['objectId', 'name'])))
  }

  export default {
    components: {
      EventFilterItem
    },
    computed: {
      filterOrganizations () {
        return getOptions(this.$store.state.organizations)
      },
      filterOrganizationValue () {
        return this.$store.state.events_filter.organization_id || ''
      },
      filterLevels () {
        return getOptions(this.$store.state.levels)
      },
      filterLevelValue () {
        return this.$store.state.events_filter.level_id || ''
      },
      filterRegions () {
        return getOptions(this.$store.state.regions)
      },
      filterRegionValue () {
        return this.$store.state.events_filter.region_id || ''
      },
      filterCategories () {
        return this.$store.getters.categoriesTree;
      },
      filterCategoryValue () {
        return this.$store.state.events_filter.category_id || ''
      }
    },
    methods: {
      dispatchFilterBy (action) {
        return function(data){
          return this.$store.dispatch(action, data);
        }
      }
    }
  }
</script>

<style>
  @import "variables.css";

  .filter-list {
    float: right;
    margin-right: 10px;
    font-size: 0;
    box-sizing: border-box;
    font-family: 'Helvetica Neue Condensed', "Roboto Condensed", sans-serif;
  }

  .filter-list button {
    display: none
  }

  @media (--header-medium) {
    .filter-list {
      float: none;
      margin: 0;
      padding-top: 1rem;
      text-align: center;
      clear: both;
    }
  }

  @media (--header-small) {
    .filter-list {
      position: fixed;
      top: var(--headerHeightSm);
      /*bottom: 0;*/
      left: 0;
      right: 0;
      padding: 2rem;
      background: #16407d;
      width: 100%;
      display: none;
      overflow-y: auto;
    }

    .filter-toggle-checkbox:checked + .filter-toggle + .filter-list {
      display: block;
      z-index: 901;
    }
  }
</style>



