<template>
  <div>

    <form @submit.prevent="submitAction" id="enroll-form" name="enroll-form" class="enroll-form forms">

      <h2 v-translate>Enrollment</h2>

      <div v-if="formSubmitted">
        <h3>Congratulation!</h3>
        <p>You have been successfully enroll to {{event.title}}</p>
      </div>
      <div v-if="!formSubmitted">
        <label v-translate>Email</label>
        <input type="email" name="mail" v-model="mail" v-validate.initial="mail" data-rules="required|email" :class="{'has-error': errors.has('mail') }" required v-bind:disabled="formSubmitted" />
        <button type="submit" class="button-primary" v-bind:disabled="formSubmitted">Send form</button>
      </div>
    </form>
  </div>
</template>

<script>
  export default {
    props: ['event'],
    data: function() {
      return {
        mail: '',
        formSubmitted: false
      }
    },
    methods: {
      submitAction (e) {
        this.$store.dispatch('saveSubscription', {
          event_id: this.event.objectId,
          event: this.event,
          email: this.mail
        })
          .then(() => {
            this.formSubmitted = true;
          })
      }
    }
  }
</script>

<style>
  .modal-container form.enroll-form {
    padding: 0;
    margin:2.5rem 0 0 0;
  }
</style>



