<template>
  <router-link :to="{ name: 'viewEvent', params: Object.assign({}, routeParams, {id: event.objectId}) }"  class="event-item">
    <div @mouseover="showOverlay = true" @mouseleave="showOverlay = false">
      <div class="event-box" v-theme-by-category="event.category_id">
        <div class="event-item--img">
          <div class="middle-helper"></div>
          <organization-image v-bind:organization_id="event.organization_id"></organization-image>
        </div>
        <div class="event-item--type"><span>
          <category-name v-bind:category_id="event.category_id"></category-name>
        </span></div>
      </div>
      <div class="event-description">{{event.title}}</div>
      <div class="event-date" v-if="timeline_mode !== 'day'">{{event.start_date|date}}<span class="event-date__end"> - {{event.end_date|date}}</span></div>

      <div class="event-overlay" v-show="showOverlay">
        <div class="event-description--body">{{event.description}}</div>
        <event-social-buttons></event-social-buttons>
      </div>
    </div>

  </router-link>
</template>

<style>
  @import "variables.css";

  .event-item {
    margin: 0 1rem 1.5rem;
    width: 16rem;
    text-decoration: none;
    position: relative;
  }

  .event-item:hover {
    box-shadow: 0 0 5px rgba(0,0,0,.5);
  }

  .event-item .event-box {
    display: table;
    table-layout: fixed;
    height: 4.2rem;
    width: 16rem;
    margin-bottom: .5rem;
    cursor: pointer;
  }

  .event-item .event-box.lowercase span {
    text-transform: none;
    font-weight: 500
  }

  .event-item .event-box.blue-red .event-item--img {
    background: #16407d;
    border: 0
  }

  .event-item .event-box.blue-red .event-item--type {
    background: #e73e42;
    color: #fff;
    border: 0
  }

  .event-item .event-box .event-item--img {
    display: table-cell;
    width: 4rem;
    position: relative;
    background: #9b9b9b;
    z-index: 1;
    border: 2px solid #9b9b9b;
  }

  .event-item .event-box .event-item--img img {
    max-height: 100%;
    max-width: 100%;
    width: auto;
    height: auto;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto
  }

  .event-item .event-box .event-item--type {
    display: table-cell;
    vertical-align: middle;
    text-transform: uppercase;
    color: #16407d;
    font-size: 1.1rem;
    font-weight: bold;
    border: 2px solid #9b9b9b;
    border-left: 0;
  }

  .event-item .event-box .event-item--type span {
    padding: 0 .7rem;
    display: block
  }

  .event-item .event-description {
    color: #000;
    font-size: 1.2rem;
    z-index: 22;
  }

  .event-item .event-date {
    color: #222;
    font-size: 1.1rem;
    font-weight: bold;
    z-index: 22;
  }

  .event-item .icon {
    width: 1.9rem;
    height: 1.9rem;
    text-align: center;
    line-height: 1.9rem;
    display: inline-block;
  }

  /* Vertical layout for timeline */
  @media (--timeline-large) {
    .event-item {
      flex: 0 1 auto;
      align-self: auto;
      order: 0;
    }
  }
  @media (--timeline-small) {
    .event-item {
      margin: 0 2rem 2rem 0rem;
      display: inline-block;
      vertical-align: top;
    }
  }

  .compact-mode .event-item {
    margin-bottom: 2rem;
    width: 6rem;
    display: inline-block;
    vertical-align: top;
  }

  .compact-mode .event-item .event-description {
    line-height: 1.1;
    font-size: 1.1rem;
    font-weight: 500;
    color: #16407d;
    word-wrap: break-word;
  }

  .compact-mode .event-item .event-box {
    width: 6rem;
  }

  .compact-mode .event-item .event-box .event-item--type {
    display: none
  }

  .event-description--body {
    font-size: 1.1rem;
    color: #000;
    height: 5.2rem;
    overflow: hidden;
  }

  .social-buttons,
  .event-description--body,
  .event-description,
  .event-date {
    padding: 0 .5rem 0;
  }

  .social-buttons {
    padding-bottom:.2rem;
  }

  .compact-mode .social-buttons,
  .compact-mode .event-description--body,
  .compact-mode .event-description,
  .compact-mode .event-date {
    padding-left: 0;
    padding-right: 0;
  }

  .compact-mode .event-date__end {
    display: none;
  }

  .event-overlay {
    position: absolute;
    top:100%;
    margin-top:1px;
    background:#fff;
    width: 16rem;
    box-shadow: 0 0 5px rgba(0,0,0,.5);
    z-index:23;
  }
  .event-overlay:before {
    position: absolute;
    top:-5px;
    background: #fff;
    width: 16rem;
    height:5px;
    content: ' ';
  }

  .compact-mode .event-overlay {
    display: none!important;
  }

  @media (--timeline-small) {
    .compact-mode .event-item:last-child {
      margin-bottom: 4rem;
    }
  }
</style>

<script>
  import OrganizationImage from './OrganizationImage';
  import CategoryName from './CategoryName';
  import EventSocialButtons from './EventSocialButtons';
  import ThemeByCategory from '../directives/ThemeByCategory';
  import fecha from 'fecha';
  import { mapState } from 'vuex';


  export default {
    components: {
      OrganizationImage,
      CategoryName,
      EventSocialButtons
    },
    directives: {
      ThemeByCategory
    },
    data: function(value) {
      return {
        showOverlay : false
      }
    },
    filters: {
      date: function (value) {
        return fecha.format(new Date(value), 'DD.MM');
      }
    },
    props: ['event'],
    computed: {
      ...mapState({
        routeParams (state){
          return state.route.params;
        },
        timeline_mode: 'timeline_mode'
      })
    }
  }
</script>




