<template>
  <modal :on-close="close">
    <h3 slot="header">Opprett event</h3>
    <div class="modal--content-scroller">
      <form @submit.prevent="submitAction" id="" name="">
        <ul v-if="errors.errors.length && 0" class="form-errors">
          <li v-for="error in errors.errors">{{ error.msg }}</li>
        </ul>
        <fieldset>
          <legend v-translate>Information</legend>
          <label v-translate>fieldTitle</label>
          <input v-model="event.title" type="text" v-validate.initial="event.title" v-bind:data-as="t('fieldTitle')"
                 data-rules="required" required
                 :class="{'has-error': errors.has('event.title') }">
          <div class="clearfix">
            <div class="col-1">
              <label v-translate>fieldCategory</label>
              <select v-model="event.category_id" v-validate.initial="event.category_id" data-rules="required" required
                      :class="{'has-error': errors.has('event.category_id') }">
                <option value="">Kategori</option>
                <optgroup v-for="optgroup in getCategoryList" v-bind:label="optgroup.name">
                  <option v-for="option in optgroup.options" v-bind:value="option.objectId">{{option.name}}</option>
                </optgroup>
              </select>
            </div>
            <div class="col-2">
              <label v-translate>fieldOrganization</label>
              <select v-model="event.organization_id" v-validate.initial="event.organization_id" data-rules="required"
                      required :class="{'has-error': errors.has('event.organization_id') }">
                <option value="">Organisasjon</option>
                <option v-for="option in getOrganizationsList" v-bind:value="option.objectId">{{option.name}}</option>
              </select>
            </div>
          </div>
          <div class="clearfix">
            <div class="col-1">
              <label v-translate>fieldLevel</label>
              <select v-model="event.level_id" v-validate.initial="event.level_id" data-rules="required" required
                      :class="{'has-error': errors.has('event.level_id') }">
                <option value="">Nivå</option>
                <option v-for="option in getLevelsList" v-bind:value="option.objectId">{{option.name}}</option>
              </select>
            </div>
            <div class="col-2">
              <label v-translate>fieldRegion</label>
              <select v-model="event.region_id" v-validate.initial="event.region_id" data-rules="required" required
                      :class="{'has-error': errors.has('event.region_id') }">
                <option value="">Region</option>
                <option v-for="option in getRegionsList" v-bind:value="option.objectId">{{option.name}}</option>
              </select>
            </div>
          </div>
          <label>Beskrivelse</label>
          <textarea v-model="event.description" v-validate.initial="event.description" data-rules="required" required
                    :class="{'has-error': errors.has('event.description') }"></textarea>
        </fieldset>
        <fieldset>
          <legend>Tid, sted og kart</legend>
          <div class="clearfix">
            <div class="col-1">
              <label>Startdato</label>
              <input v-model="event.start_date" type="date" name="start" v-validate.initial="event.start_date"
                     data-rules="required" required :class="{'has-error': errors.has('event.start_date') }"
                     placeholder="Thu Nov 03 2016">
            </div>
            <div class="col-2">
              <label>Sluttdato</label>
              <input v-model="event.end_date" type="date" name="end" v-validate.initial="event.end_date"
                     data-rules="required" required :class="{'has-error': errors.has('event.end_date') }"
                     placeholder="Thu Nov 03 2016">
            </div>
          </div>
          <div class="location--wrap">
            <label>Høyreklikk med pekeren for å markere sted</label>
          </div>
          <location-selector-map
            v-bind:latitude="event.geo_latitude"
            v-bind:longitude="event.geo_longitude"
            v-bind:zoom="event.geo_map_zoom"
            v-bind:callback="mapCallback"
          ></location-selector-map>
          <location-selector-autocomplete
            v-bind:latitude="event.geo_latitude"
            v-bind:longitude="event.geo_longitude"
            v-bind:callback="autocompleteCallback"
          ></location-selector-autocomplete>
          <input type="hidden" name="geo_latitude" v-model="event.geo_latitude" value="">
          <input type="hidden" name="geo_longitude" v-model="event.geo_longitude" value="">
          <input type="hidden" name="geo_map_zoom" v-model="event.geo_map_zoom" value="">


          <label>Posisjonsbeskrivelse
            <small>(valgfri)</small>
          </label>
          <textarea v-model="event.geo_description"></textarea>
        </fieldset>
        <!-- fieldset>
          <legend>Faner</legend>
          <label>FANE  1</label>
          <div class="pull-right">
            <div class="close-group">✕</div>
          </div>
          <div class="clearfix">
            <div class="col-1">
              <label>Fane tittel</label>
              <input v-model="tabName" type="text" name="" placeholder="" class="u-full-width">
            </div>
            <div class="col-2">
              <label>Fane kort tittel</label>
              <input type="text" name="login_name" placeholder="" class="u-full-width">
            </div>
          </div>
          <label>Fane beskrivelse</label>
          <textarea></textarea>
          <button>Add FANE</button>
        </fieldset -->
        <button type="submit" v-translate>buttonSave</button>
      </form>

    </div>
  </modal>
</template>


<style>
  @import "variables.css";

  .pac-container {
    z-index: 9999999;
  }

  .location--wrap {
    overflow: hidden;
    margin-bottom: .6rem;
    height: 2.6rem;
  }

  .location--wrap label {
    top: .5rem;
    position: relative;
    z-index: 1;
  }

  .button.button—-remove-marker {
    color: red;
    float: right;
    height: 2.4rem;
    line-height: 2.6rem;
    padding: 0 1rem;
    margin: -2rem 0 0 0;
    width: auto;
    border: 1px solid #ddd;
    background: #fff;
    border-radius: 2px;
    letter-spacing: .1rem;
    text-transform: uppercase;
    cursor: pointer;
    font-weight: bold;
    font-size: 1.1rem;
    position: relative;
    z-index: 2;
  }

  .button—-remove-marker:hover {
    border-color: #888;
  }
</style>

<script>
  import {map, pick, clone} from 'lodash';
  import { mapState } from 'vuex';
  import fecha from 'fecha'
  import router from '../router'
  import Modal from './Modal'
  import LocationSelectorMap from './LocationSelectorMap'
  import LocationSelectorAutocomplete from './LocationSelectorAutocomplete'

  function getOptions(data) {
    return map(data, (v => pick(v, ['objectId', 'name'])))
  }

  export default {
    data () {
      return {
        event: {}
      }
    },
    mounted () {
      this.event = this.prepareEventDataForForm(clone(this.$store.state.event));
    },
    components: {
      Modal,
      LocationSelectorMap,
      LocationSelectorAutocomplete
    },
    computed: {
      getOrganizationsList () {
        return getOptions(this.$store.state.organizations);
      },
      getLevelsList () {
        return getOptions(this.$store.state.levels);
      },
      getRegionsList () {
        return getOptions(this.$store.state.regions);
      },
      getCategoryList () {
        return this.$store.getters.categoriesTree;
      },
    },
    methods: {
      prepareEventDataForForm(event) {
        if (!event) {
          return event;
        }

        if (event.start_date) {
          event.start_date = fecha.format(new Date(event.start_date), 'YYYY-MM-DD');
        }
        if (event.end_date) {
          event.end_date = fecha.format(new Date(event.end_date), 'YYYY-MM-DD');
        }
        return event;
      },
      prepareEventDataForSave(event) {
        if (!event) {
          return event;
        }

        if (event.start_date) {
          event.start_date = fecha.parse(event.start_date, 'YYYY-MM-DD').getTime();
        }

        if (event.end_date) {
          event.end_date = fecha.parse(event.end_date, 'YYYY-MM-DD').getTime();
        }

        return event;
      },
      close: function () {
        router.push({name: 'date', params: this.$route.params})
      },
      mapCallback(lat, long, zoom) {
        this.event.geo_latitude = lat;
        this.event.geo_longitude = long;
        this.event.geo_map_zoom = zoom;
      },
      autocompleteCallback(lat, long) {
        this.event.geo_latitude = lat;
        this.event.geo_longitude = long;
      },
      submitAction(e) {
        this.$validator.validateAll();

        if (this.errors.any()) {
          e.preventDefault();
          return;
        }

        var event = this.prepareEventDataForSave(clone(this.event));
        this.$store.dispatch('saveEvent', event);
      }
    }
  }
</script>



