<template>
    <div class="gmap" id="gmap"></div>
</template>

<script>
  import GoogleMapsLoader from '../helpers/GoogleMaps'
  import GMaps from 'gmaps'

  export default {
    props: ['event'],
    data: function() {
      return {
        map: undefined,
        google: undefined,
        gmapSettings: {
          div: '#gmap',
          zoom: this.event.geo_map_zoom || 8,
          lat: this.event.geo_latitude,
          lng: this.event.geo_longitude,
          scrollwheel: false,
          panControl: false,
          mapTypeControl: false,
          overviewMapControl: false,
          streetViewControl: false
        }
      }
    },
    mounted () {
      GoogleMapsLoader.load((google) => {
        this.google = google;
        this.map = new GMaps(this.gmapSettings);

        this.map.setCenter({
          lat: this.event.geo_latitude,
          lng: this.event.geo_longitude,
        });

        this.map.addMarker({
          lat: this.event.geo_latitude,
          lng: this.event.geo_longitude,
        });
      });
    }
  }
</script>


<style>
  .gmap {
    height: 250px;
    margin-bottom: 1rem;
  }
</style>



