import Backendless from '../Backendless';

function Categories(args) {
  args = args || {};

  this.updated = args.updated || null;
  this.name = args.name || null;
  this.objectId = args.objectId || null;
  this.created = args.created || null;
  this.ownerId = args.ownerId || null;

  this.___class = "Categories";

  this.parent_id = args.parent_id || null;
}

function CategoryClass() {
  let loadedCategories;

  const query = new Backendless.DataQuery();
  query.options = {
    pageSize: 100
  };

  const categoryCollection = Backendless.Persistence.of(Categories);

  function getCategories () {
    if (!loadedCategories) {
      return loadCategories();
    }

    return getCachedCategories();
  };

  function loadCategories () {
    return categoryCollection.find(query)
      .then((response) => response.data)
      .then(setCachedCategories)
      .catch((e) => {
        console.error(e);
        Promise.reject(e);
      });
  }

  function setCachedCategories (data) {
    loadedCategories = data;
    return getCachedCategories();
  }

  function getCachedCategories () {
    return Promise.resolve(loadedCategories);
  }

  this.getAll = getCategories;
}

export default CategoryClass;



