<template>
  <img v-if="organizationImage" v-bind:src="organizationImage" v-bind:alt="organizationName"
       v-bind:title="organizationName"/>
</template>

<script>
  import _ from 'lodash';

  export default {
    props: ['organization_id'],
    computed: {
      organization () {
        return _.find(this.$store.state.organizations, {'objectId': this.organization_id})
      },
      organizationImage () {
        const image = _.get(this.organization, 'small_logo');
        if (!image) {
          return require('../assets/img/logo-grey.svg');
        }
        return image;
      },
      organizationName () {
        return _.get(this.organization, 'name', false);
      }
    }
  }
</script>




