<template>

  <modal :modal-class="'event-inner'" :on-close="close">
    <div slot="header" class="modal--header--parent" v-theme-by-category="event.category_id" v-if="event">
      <div class="modal--left-column"><organization-image v-bind:organization_id="event.organization_id"></organization-image></div>
      <div class="modal--right-column" v-theme-by-category="event.category_id">
        <h1 class="sub-section"><category-name v-bind:category_id="event.category_id"></category-name></h1>
      </div>
    </div>
    <div class="modal--content" v-if="event">
      <div class="modal--left-column">
        <div class="modal--sidebar">
          <!--<p>Ipsa repellendus itaque nemo atque expedita aut voluptatem. Voluptatem aspernatur cum iste explicabo aut quod placeat est qui. Velit et rem quod nihil. Ea itaque magni reiciendis quisquam. Reiciendis tempore perferendis rem unde corporis quae.</p>-->
          <ul class="modal-tab--switcher">
            <li v-for="(tab, key) in tabs" @click="setActiveTab(key)" v-bind:class="{'active': (currentTab == key)}" v-translate>{{tab.name}}</li>
          </ul>
        </div>
      </div>
      <div class="add-scroll">
        <div class="modal--right-column">
          <component v-bind:is="currentView" v-bind:event="event">
            <!-- component changes when vm.currentView changes! -->
          </component>
        </div>
      </div>
      <div class="modal--footer">
        <button-edit-event v-if="login" v-bind:id="event.objectId" v-bind:klass="'modal--event-edit'"><i class="icon-pencil"></i><span v-translate>Edit Event</span></button-edit-event>
        <button-remove-event v-if="login" v-bind:id="event.objectId" v-bind:klass="'modal--event-delete'"><i class="icon-cancel"></i><span v-translate>Delete Event</span></button-remove-event>
        <!--<button @click="alert" class="modal&#45;&#45;event-print"><i class="icon-print"></i><span>Utskrift</span></button>-->

        <event-social-buttons></event-social-buttons>
      </div>
    </div>
  </modal>
</template>
<script>
  import { mapState } from 'vuex';
  import _ from 'lodash';
  import router from '../router/index';
  import OrganizationImage from './OrganizationImage';
  import CategoryName from './CategoryName';
  import EventViewMainPage from './EventViewMainPage';
  import EventViewDatePlacePage from './EventViewDatePlacePage';
  import EventViewEnrollmentPage from './EventViewEnrollmentPage';
  import ButtonEditEvent from './ButtonEditEvent';
  import ButtonRemoveEvent from './ButtonRemoveEvent';
  import EventSocialButtons from './EventSocialButtons';
  import Modal from './Modal';
  import ThemeByCategory from '../directives/ThemeByCategory';

  export default {
    components: {
      Modal,
      OrganizationImage,
      CategoryName,
      EventViewMainPage,
      EventViewEnrollmentPage,
      EventViewDatePlacePage,
      ButtonEditEvent,
      ButtonRemoveEvent,
      EventSocialButtons
    },
    directives: {
      ThemeByCategory
    },
    data: function() {
      return {
        isActive: false,
        tabs: {
          main: {
            name: 'Information',
            component: 'event-view-main-page'
          },
          date: {
            name: 'Time, Location, Map',
            component: 'event-view-date-place-page'
          },
          enroll: {
            name: 'Enrollment',
            component: 'event-view-enrollment-page'
          }
        },
        currentTab: undefined,
        currentView: undefined
      }
    },
    mounted () {
      this.setActiveTab('main');
    },
    computed: {
      ...mapState({
        event: function(state) {
          return state.event;
        },
        login(state) {
          return !!state.user;
        },
      })
    },
    methods: {
      close: function(){
        router.push({name:'date', params: this.$route.params})
      },
      alert: function() {
        alert('Sorry this part in development!');
      },
      setActiveTab: function(name) {
        this.currentTab = name;
        this.currentView = this.tabs[name].component;
      }
    }
  }
</script>

<style>
  @import "variables.css";

  .event-inner h1 {
    text-transform: uppercase;
  }

  .event-inner .modal-container {
    width: 80rem;
    height:60rem;
  }

  .event-inner .close {
    position: absolute;
    z-index: 10000;
    top: 0;
    right: 0;
    line-height: 25px;
    line-height: 2.5rem;
    width: 25px;
    width: 2.5rem;
    cursor: pointer;
    text-align: center;
    font-family: Arial, sans-serif;
    background: #fff;
    background: rgba(255,255,255,0.3);
    color: #000;
  }
  .event-inner .modal--header {
    height: 2rem;
    overflow: hidden;
    display: table-row-group;
    table-layout: fixed;
    padding:0;;
  }
  .event-inner .modal--header--parent {
    height: 2rem;
    overflow: hidden;
    display: table-row;
  }
  .event-inner .modal--header .modal--left-column {
    background-color: #989fa3;
    width: 28.7rem;
    white-space: normal;
    display: table-cell;
  }
  .event-inner .modal--header .modal--left-column img {
    height: 9rem;
    position: absolute;
    z-index: 1;
    top: 7rem;
    left: 10px;
    margin-top: -65px;
  }
  .event-inner .modal--header .modal--right-column {
    background-color: #16407d;
    width: 68rem;
    display: table-cell;
  }
  .event-inner .modal--header .modal--right-column h1 {
    padding: 1.5rem 2.5rem 0;
    font: 400 2.2rem/1.2 'Helvetica Neue Condensed', "Roboto Condensed", sans-serif;
  }
  .event-inner .modal--content {
    display: table-row;
  }
  .event-inner .modal--content .modal--left-column {
    display: table-cell;
    width: 27rem;
  }
  .event-inner .modal--content .modal--left-column .modal--sidebar {
    padding: 3rem 2.5rem 11rem 1.5rem;
    box-sizing: border-box;
  }
  .event-inner .modal--content .modal--left-column .modal--sidebar p {
    font-size: 1.2rem;
    line-height: 1.1;
    margin: 0 0 1em;
  }
  .event-inner .modal-tab--switcher {
    margin: 0;
    padding: 0;
    list-style-type: none;
    font: 400 1.3rem/1.2 'Helvetica Neue Condensed', "Roboto Condensed", sans-serif;
  }
  .event-inner .modal-tab--switcher li {
    margin: 0;
    padding: 0.8em 0 0 2rem;
    position: relative;
    cursor: pointer;
  }

  .event-inner .modal-tab--switcher li:before {
    display: block;
    position: absolute;
    left: 0;
    content: '';
    top: 1.1em;
    width: 0.8rem;
    height: 0.8rem;
    background: #16407d;
  }
  .event-inner .modal-tab--switcher li.active:before {
    background: #e24343;
  }

  .event-inner .modal-tab--switcher li.active:after {
    display: block;
    position: absolute;
    right: 0;
    top: .7em;
    background: url("../assets/img/delimiter-active.png") no-repeat;
    width: 18px;
    width: 1.8rem;
    height: 18px;
    height: 1.8rem;
    margin-right: -34px;
    z-index: 10;
    content: '';
  }

  .event-inner .modal--content .modal--right-column {
    width: 68rem;
    display: table-cell;
    padding: 0 2rem;
  }
  .event-inner .modal--content .modal--right-column:before {
    position: absolute;
    top: 0;
    margin-left:-2rem;
    bottom: 0;
    width: 0.7rem;
    content: '';
    background: linear-gradient(to right, rgba(0,0,0,0.1) 0, rgba(46,46,46,0.25) 18%, rgba(255,255,255,0) 100%);
  }
  .event-inner .modal--content .modal--right-column p {
    font-size: 1em;
  }
  .event-inner .modal--content .modal--right-column h2 {
    color: #2d72a0;
    font: 400 2.2rem/1.2 'Helvetica Neue Condensed', "Roboto Condensed", sans-serif;
  }
  .event-inner .modal--footer {
    opacity: 1;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 190px;
    width: 19rem;
    padding: 0 1.5rem 1.5rem;
  }
  .event-inner .modal--footer .icon {
    color: #fff;
    background: #16407d;
    width: 2.4rem;
    height: 2.4rem;
    text-align: center;
    line-height: 2.4rem;
    cursor: pointer;
    display: inline-block;
  }
  .event-inner .modal--footer .icon:hover {
    background: #ed4044;
  }
  .event-inner .modal--footer .modal--event-print,
  .event-inner .modal--footer .modal--event-edit,
  .event-inner .modal--footer .modal--event-delete {
    background: #ed4044;
    border: 1px solid #fff;
    display: block;
    height: auto;
    margin: 0 -0.1rem 0.8rem;
    padding: 0.3rem 1rem;
    width: 100%;
    text-align: left;
    color: #fff;
    font: 700 1.6rem/1.2 'Helvetica Neue Condensed', "Roboto Condensed", sans-serif;
    text-transform: uppercase;
  }
  .event-inner .modal--footer .modal--event-print:hover {
    color: #000;
  }

  .event-inner .modal--footer .modal--event-edit {
    color: #FFF;
    background-color: #33C3F0;
    border-color: #33C3F0;
  }

  .event-inner .modal--footer .modal--event-edit:hover {
    background-color: #1EAEDB;
    border-color: #1EAEDB;
  }

  .event-inner .modal--footer .modal--event-delete {
    ackground-color: #fefefe;
    background: none;
    border-color: #666;
    color: #666;
  }

  .event-inner .modal--footer .modal--event-delete:hover {
    border-color: #000;
    color: #000;
  }

  .event-inner .modal--footer .modal--event-print i,
  .event-inner .modal--footer .modal--event-edit i,
  .event-inner .modal--footer .modal--event-delete i {
    float: right;
  }
  .event-inner .modal--footer .modal--event-print p {
    font-size: 1.3rem;
  }
  .event-inner .modal--footer .modal--event-print h2 {
    color: #2d72a0;
    font: 400 2.2rem/1.2 'Helvetica Neue Condensed', "Roboto Condensed", sans-serif;
  }
  .event-inner .modal--footer .modal-tab--switcher {
    margin: 0;
    padding: 0;
    list-style-type: none;
    font: 400 13px/1.2 'Helvetica Neue Condensed', "Roboto Condensed", sans-serif;
  }
  .event-inner .modal--footer .modal-tab--switcher li {
    margin: 0;
    padding: 0.8em 0 0 2rem;
    position: relative;
    cursor: pointer;
  }
  .event-inner .modal--footer .modal-tab--switcher li:before {
    display: block;
    position: absolute;
    left: 0;
    content: '';
    top: 1.1em;
    width: 0.8rem;
    height: 0.8rem;
    background: #16407d;
  }
  .event-inner .modal--footer .modal-tab--switcher li.active:before {
    background: #e24343;
  }
  .event-inner .modal--footer .modal--sidebar {
    padding: 3rem 2.5rem 11rem 1.5rem;
  }
  .event-inner .modal--footer .modal--sidebar p {
    font-size: 1.2rem;
    line-height: 1.1;
    margin: 0 0 1em;
  }

  .social-buttons i {
    background: #16407d !important;
    color: #fff;
    border: 0;
    font-size: 1.5rem;
    padding: 0;
  }

  .social-buttons i:hover {
    background: #ed4044 !important;
  }

  .event-inner .modal--header .modal--right-column.blue-red {
    background: #e73e42;
  }

  .event-inner .modal--header--parent.blue-red .modal--left-column{
    background: #16407D;
  }

  .event-inner .modal--header .modal--right-column.grey-black {
    background: #000;
  }

  .event-inner .modal--header .modal--right-column.grey-blue {
    background: #16407D;
  }

  .add-scroll {
    overflow-y: scroll;
    height: 49rem;
  }


</style>



