<template>
  <button @click.prevent="onClick" v-bind:class="[ klass ]"><slot></slot></button>
</template>

<script>
  import router from '../router'

  export default {
    props: ['klass', 'id'],
    methods: {
      onClick () {
        router.push({ name: 'editEvent', params: Object.assign({}, this.$route.params, {id: this.id}) })
      }
    }
  }
</script>



