<template>
  <div class="location-selector-body">
    <a v-if="isMarkersExist" v-on:click="clickCallback" class="button button—-remove-marker">Slett lokasjon</a>
    <div class="gmap" id="gmap"></div>
  </div>
</template>

<script>
  import _ from 'lodash/core';
  import GMaps from 'gmaps'
  import GoogleMapsLoader from '../helpers/GoogleMaps'

  export default {
    props: ['latitude', 'longitude', 'zoom', 'callback'],
    data: function () {
      return {
        map: undefined,
        google: undefined,
        gmapSettings: {
          div: '#gmap',
          zoom: this.zoom || 8,
          lat: this.latitude || 59.89444803635239,
          lng: this.longitude || 10.79183578491211,
          scrollwheel: false,
          panControl: false,
          mapTypeControl: false,
          overviewMapControl: false,
          streetViewControl: false
        }
      }
    },
    mounted () {
      GoogleMapsLoader.load((google) => {
        this.google = google;
        this.map = new GMaps(this.gmapSettings);
        this.map.on("rightclick", (event) => {
          if (typeof this.callback === 'function') {
            this.callback(event.latLng.lat(), event.latLng.lng(), this.map.getZoom());
          }
        });
        this.map.on("zoom_changed", () => {
          if (typeof this.callback === 'function') {
            this.callback(this.latitude, this.longitude, this.map.getZoom());
          }
          this.map.setCenter(this.latitude, this.longitude);
        });
        if (this.latitude && this.longitude) {
          this.addMarker(this.latitude, this.longitude)
        }
      });
    },
    beforeDestroy () {
      this.unmountMap()
    },
    computed: {
      isMarkersExist () {
        return this.map && this.map.markers ? this.map.markers.length > 0 : false
      },
      marker () {
        return [this.latitude, this.longitude];
      }
    },
    watch: {
      marker (data) {
        this.addMarker.apply(this, data);
      },
      zoom (data) {
        data = parseInt(data);

        if (!data || !this.map || (this.map && this.map.getZoom() === data)) {
          return;
        }

        this.map.setZoom(data);
      },
    },
    methods: {
      addMarker (lat, lng) {
        if (!this.map) {
          return;
        }
        this.removeMarkers();
        if (lat && lng) {
          this.map.addMarker({
            lat: lat,
            lng: lng,
          });
          this.map.setCenter(lat, lng);
        }
      },
      removeMarkers () {
        if (!this.map) {
          return;
        }
        _.each(this.map.markers, function (marker) {
          marker.setMap(null);
        });
        this.map.markers = [];
      },
      clickCallback () {
        if (typeof this.callback === 'function') {
          this.callback('', '', this.map.getZoom());
        }
      },
      unmountMap () {
        if (!this.google) {
          return;
        }
        this.google.maps.event.clearInstanceListeners(window);
        this.google.maps.event.clearInstanceListeners(document);
        this.google.maps.event.clearInstanceListeners(this.$el.getElementsByClassName('gmap')[0]);
        this.removeMarkers();
        this.map = undefined;
        this.google = undefined;
      }
    }

  }
</script>

<style>
  .gmap {
    height: 250px;
    margin-bottom: 1rem;
  }

  .location-selector-body {
    position: relative;
  }

  .location-selector-body .button.button—-remove-marker {
    position: absolute;
    right: 1rem;
    top: 3rem;
  }

</style>



