<template>
  <header class="header">
    <h1>
      <img class="logo-chevron" v-bind:src="mapImg" alt="">
      <span>NCF Offisiell Kalender</span>
    </h1>
    <event-filter></event-filter>
  </header>
</template>

<style>
  @import "variables.css";

  .header {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-content: stretch;
    align-items: center;
    background: var(--headerBgColor);
    height: var(--headerHeightLg);
    padding-top: 0px;
    box-sizing: border-box;
    z-index: 999999;
  }

  @media (--header-medium) {
    .header {
      height: var(--headerHeightMd);
      display: flex;
      flex-direction: column;
      flex-wrap: nowrap;
      justify-content: flex-start;
      align-content: stretch;
      align-items: stretch;
    }

  }

  .header h1 {
    order: 0;
    flex: 0 0 405px;
    align-self: auto;
    background: url("../assets/img/title.png") no-repeat top right;
    background-size: 336px 13px;
    box-sizing: border-box;
    width: 405px;
    height: 13px;
    margin: 5px 0;
    padding-left: 50px;
    position: relative;
    & span {
      font-size: 0;
      position: absolute;
      left:-999em;
    }
  }

  .logo-chevron {
    position: absolute;
    left: 23px;
    top: -12px;
    width: 32px;
  }

  @media (--header-medium) {
    .header h1 {
        flex: 0 0 auto;
        margin: 13px auto 5px;
        width: 390px;
        float: none;
        text-align: center;
    }

    .logo-chevron {
      top:-10px;
      width: 23px;
    }
  }

  @media (--header-small) {
    .header h1 {
      background-image: url("../assets/img/title-md.png");
      background-size: 216px 13px;
      width: 265px;
      flex: 0 0 265px;
    }

    .logo-chevron {
      left: 6px;
    }
  }

</style>

<script>
  import EventFilter from './EventFilter'

  export default {
    components: {
      EventFilter
    },
    computed: {
      mapImg () {
        var image = require('../assets/img/logo.svg');
        return image;
      }
    }
  }
</script>



