import Vue from 'vue'
import Router from 'vue-router'
import fecha from 'fecha'
import store from '../store'
import EventForm from '../components/EventForm'
import EventView from '../components/EventView'
import LoginForm from '../components/LoginForm'

Vue.use(Router)

const router = new Router({
  mode: 'hash',
  routes: [
    {
      path: '/'
    },
    {
      path: '/:date',
      name: 'date'
    },
    {
      path: '/:date/login',
      component: LoginForm,
      name: 'login',
      meta: {
        loadEvent: true
      }
    },
    {
      path: '/:date/add',
      component: EventForm,
      name: 'addEvent',
      meta: {
        requiresAuth: true,
        loadNewEvent: true
      }
    },
    {
      path: '/:date/view/:id',
      component: EventView,
      name: 'viewEvent',
      meta: {
        loadEvent: true
      }
    },
    {
      path: '/:date/edit/:id',
      component: EventForm,
      name: 'editEvent',
      meta: {
        requiresAuth: true,
        loadEvent: true
      }
    }
  ]
})

router.beforeEach((to, from, next) => {
  if (!to.params.date || !to.params.date.match(/^\d{4}-\d{2}-\d{2}$/g) || (String(new Date(to.params.date)) === "Invalid Date")) {
    next('/' + fecha.format(new Date(), 'YYYY-MM-DD'));
  } else {
    next();
  }
});

router.beforeEach((to, from, next) => {
  if (to.meta.requiresAuth === true) {
    next(!!store.state.user || {name: 'login', params: Object.assign({}, to.params)})
  } else {
    next()
  }
});

router.beforeEach((to, from, next) => {
  if (to.meta.loadEvent === true) {
    store.dispatch('loadEvent', to.params.id);
  }
  if (to.meta.loadNewEvent === true) {
    store.dispatch('loadEvent', undefined);
  }
  next();
});

router.afterEach((to, from) => {
  if (from.meta.loadEvent === true || from.meta.loadNewEvent === true) {
    store.dispatch('unloadEvent');
  }
});

export default router;




