<template>
  <td class="calendar-event" v-bind:class="{ prevmonth: event.prevClass, nextmonth: event.nextClass, currentday: event.currentClass, activeday: event.activeClass, event: event.events }">
    <router-link :to="{ name: 'date', params: Object.assign({}, {date: event.date}) }"  v-if="event.events">{{ event.day }}</router-link>
    <span v-else>{{ event.day }}</span>
  </td>
</template>


<script>
  export default {
    props: ['event']
  }
</script>

<style>
  .calendar-event.currentday { /* Today */
    color: blue;
  }
  .calendar-event.activeday {
    border-color:#e73e42 !important;
  }

  .calendar td > a,
  .calendar td > span {
    text-decoration: none;
    width: 2.5em;
    text-align: center;
    font-size: 14px;
    display: inline-block;
    height: 2.2em;
    line-height: 2.3em;
    padding:0 1rem;
    box-sizing: border-box;
  }

  .calendar tbody td.event:hover {
    border: 2px solid #16407d
  }

  .calendar td > a  {
    color: #000;
  }

  .calendar td.prevmonth.event > a,
  .calendar td.nextmonth.event > a {
    color:#ddd;
  }

  .calendar .event {
    cursor: pointer;
    position: relative;
  }

  .calendar .event:after {
    background: #16407d;
    border-radius: 50%;
    bottom: 1px;
    display: block;
    content: '';
    height: 6px;
    left: 53%;
    margin: -.25em 0 0 -.25em;
    position: absolute;
    width: 6px
  }

  .calendar td.nextmonth.event:after,
  .calendar td.prevmonth.event:after{
    background:  #ddd;
  }

  .calendar .event.currentday:after {
    background: #e73e42
  }

</style>



