<template>
  <div v-bind="getAddress">{{markerAddress}}</div>
</template>

<script>
  export default {
    props: ['event'],
    data: function() {
      return {
        markerAddress: ''
      }
    },
    computed: {
      getAddress: function() {
        var getJSON = function (url, callback) {
          var request = new XMLHttpRequest();
          request.open("get", url, true);
          request.responseType = "json";
          request.onload = function () {
            var status = request.status;
            if (status == 200) {
              callback(null, request.response);
            } else {
              callback(status);
            }
          };
          request.send();
        };

        var url = '//maps.googleapis.com/maps/api/geocode/json?latlng=' + this.event.geo_latitude + ',' + this.event.geo_longitude + '&sensor=true&language=no';
        getJSON(url, (err, data) => {
          this.markerAddress = (data.status == 'OK') ? data.results[0].formatted_address : '';
      });
      }
    }
  }
</script>




