<template>
  <div class="calendar">
    <header>
      <h2>{{monthTitle}} <span>{{currentYear}}</span></h2>
      <button @click="goPrevious" class="btn-prev fontawesome-angle-left"><i class="fa fa-chevron-left"></i>◀</button>
      <button @click="goNext" class="btn-next fontawesome-angle-right"><i class="fa fa-chevron-right"></i>▶</button>
    </header>
    <table>
      <thead>
      <tr>
        <td v-for="value in daysLabels">{{value}}</td>
      </tr>
      </thead>
      <tbody>
        <tr>
          <calendar-td v-for="value in calendarData" v-bind:event="value"></calendar-td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
  import fecha from 'fecha';
  import _ from 'lodash/core';
  import CalendarTd from './CalendarTd';

  export default {
   data: function() {
       return {
         daysLabels: ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'],
         daysInMonth: [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31],
         currentDate: new Date(this.$store.state.route.params.date)
       }
    },
    components: {
      CalendarTd
    },
    computed: {
      currentDay: function(){
        return this.currentDate.getDay();
      },
      currentMonth: function(){
        return this.currentDate.getMonth();
      },
      currentYear: function(){
        return this.currentDate.getFullYear();
      },
      daysInCurrentMonth: function(){
        return this.daysInMonth[this.currentMonth]
      },
      daysInPreviousMonth: function(){
        return this.daysInMonth[this.currentMonth === 0 ? 11 : this.currentMonth - 1]
      },
      daysInNextMonth: function(){
        return this.daysInMonth[this.currentMonth === 11 ? 0 : this.currentMonth + 1]
      },
      firstDayInCurrentMonth: function(){
        return new Date(this.currentYear, this.currentMonth, 1).getDay();
      },
      lastDayInCurrentMonth: function(){
        return new Date(this.currentYear, this.currentMonth, this.daysInCurrentMonth).getDay();
      },
      monthTitle: function(){
        return fecha.format(this.currentDate, 'MMMM'); // Month
      },
      calendarData: function(){
        var start = new Date(this.currentDate.getFullYear(), this.currentDate.getMonth(), - this.firstDayInCurrentMonth + 1);
        var end = new Date(this.currentDate.getFullYear(), this.currentDate.getMonth() + 1, 6 - this.lastDayInCurrentMonth);
        var calendarObj = [];

        while(start < end){
          var newDate = start.setDate(start.getDate() + 1);
          var dateKey = fecha.format(start, 'YYYY-MM-DD');
          start = new Date(newDate - 1);

          calendarObj.push({
            day: fecha.format(start, 'D'),
            date: dateKey,
            prevClass: new Date(this.currentDate.getFullYear(), this.currentDate.getMonth()) > start,
            nextClass: new Date(this.currentDate.getFullYear(), this.currentDate.getMonth() + 1) < start,
            activeClass: dateKey === this.$store.state.route.params.date,
            currentClass: dateKey === fecha.format(new Date(), 'YYYY-MM-DD'),
            events: this.events[dateKey] || false
          });
        }
        return calendarObj;
      },
      events (state){
        return _.chain(this.$store.state.events)
          .reduce(function(result, value) {
            var key = fecha.format(new Date(value.start_date), 'YYYY-MM-DD');
            result[key] ? result[key]++ : result[key] = 1;
            return result;
          }, {})
          .value()
      }
    },
    methods: {
      goPrevious: function() {
        this.currentDate = new Date(this.currentYear, this.currentMonth - 1, 1);
      },
      goNext: function() {
        this.currentDate = new Date(this.currentYear, this.currentMonth + 1, 1);
      }
    }
  }
</script>

<style>
  @import "variables.css";

  .calendar {
    transition: all .3s ease;
    text-align: center;
    overflow: hidden;
    margin:0 0 2rem;
  }

  .calendar i {
    font-size: 1.8rem;
    padding-top: 3px;
    color: #16407d
  }

  .calendar header {
    position: relative
  }

  .calendar header button {
    border: 0;
    background: #fff;
  }

  .calendar h2 {
    font-size: 2.7rem;
    margin-bottom: 0;
  }

  .calendar h2 span {
    font-weight: normal
  }

  .calendar tbody {
    color: #7c8a95;
  }

  .calendar table {
    margin-left: 3.7rem;
    border-collapse: collapse;
    border-spacing: 0;
    margin-top:1rem;
  }

  .calendar td {
    border: 2px solid transparent;
    display: inline-block;
    color: #000;
    text-align: center;
  }

  .calendar thead td {
    font-weight: normal;
    text-transform: uppercase;
    font-size: 11px;
    color: #5e6066;
    width: 3.5rem
  }

  .calendar .btn-prev {
    left: 4rem
  }

  .calendar .btn-next {
    right: 4.1rem
  }

  .calendar .switch {
    text-align: left;
    color: #16407d;
    margin-left: -1rem;
    margin-top: 1rem;
  }

  .calendar .switch a {
    color: #16407d !important;
    font-size: 1.3rem
  }

  .calendar .prevmonth, .calendar .nextmonth {
    color: #ddd
  }

  .calendar .prevmonth:hover, .calendar .nextmonth:hover {
    border: 2px solid transparent
  }

  .calendar .btn-prev, .calendar .btn-next {
    text-decoration: none;
    height: 2em;
    font-size: .75em;
    line-height: 2em;
    margin: -1em;
    position: absolute;
    top: 43%;
    width: 2em;
    color: #16407d
  }

  .calendar .btn-prev:hover, .calendar .btn-next:hover {
    color: #000
  }

  .calendar table {
    text-align: left;
  }

  @media (--timeline-small) {
    .calendar {
      transition: none;
      border: 0;
      z-index: 1001;
      width: 100%;
      left: 0;
      padding: 0;
    }
    .calendar table {
      margin-left: 0;
      width: 100%;
    }
    .calendar thead td,
    .calendar td {
      width: 10%;
      font-size: 1.5rem;
      padding: 2% 0;
    }
    .calendar h2 {
      margin-bottom: 3rem;
    }
  }

  @media (--phone) {
    .calendar thead td,
    .calendar td {
      padding: 0;
    }
  }
</style>



