import Backendless from '../Backendless';

function Levels(args) {
  args = args || {};

  this.ownerId = args.ownerId || null;
  this.created = args.created || null;
  this.objectId = args.objectId || null;
  this.updated = args.updated || null;
  this.name = args.name || null;

  this.___class = "Levels";
}

function LevelClass() {
  let loadedLevels;

  const query = new Backendless.DataQuery();
  query.options = {
    pageSize: 100
  };

  const levelCollection = Backendless.Persistence.of(Levels);

  function getLevels() {
    if (!loadedLevels) {
      loadedLevels = loadLevels();
      return loadedLevels;
    }

    return getCachedLevels();
  }

  function loadLevels() {
    return levelCollection.find(query)
      .then((response) => response.data)
      .then(setCachedLevels)
      .catch((e) => {
        console.error(e);
        Promise.reject(e);
      });
  }

  function setCachedLevels(data) {
    loadedLevels = data;
    return getCachedLevels();
  }

  function getCachedLevels() {
    return Promise.resolve(loadedLevels);
  }

  this.getAll = getLevels;
}

export default LevelClass;



