import Backendless from '../Backendless';

function Regions(args) {
  args = args || {};
  this.name = args.name || "";
}

function RegionClass() {
  let loadedRegions;

  const query = new Backendless.DataQuery();
  query.options = {
    pageSize: 100
  };

  const regionCollection = Backendless.Persistence.of(Regions);

  function getRegions() {
    if (!loadedRegions) {
      loadedRegions = loadRegions();
      return loadedRegions;
    }

    return getCachedRegions();
  }

  function loadRegions() {
    return regionCollection.find(query)
      .then((response) => response.data)
      .then(setCachedRegions)
      .catch((e) => {
        console.error(e);
        Promise.reject(e);
      });
  }

  function setCachedRegions(data) {
    loadedRegions = data;
    return getCachedRegions();
  }

  function getCachedRegions() {
    return Promise.resolve(loadedRegions);
  }

  this.getAll = getRegions;
}

export default RegionClass;



