<template>
  <div class="loader" v-bind:class="{on: loader}">
    <div class="spinner" v-if="loader">
      <div class="bounce1"></div>
      <div class="bounce2"></div>
    </div>
  </div>
</template>
<style>
  .loader {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(255,255,255,.7);
    z-index:10000;
    display: none;
  }

  .loader.on {
    display: block;
  }

  .spinner {
    margin: 20vh auto 0;
    width: 206px;
    height: 110px;
    background-image: url(../assets/img/bycicle.svg);
    background-size:206px 110px;
    background-repeat:none;
    background-position: 50% 50%;
    text-align: center;
  }
</style>
<script>
  export default {
    computed: {
      loader () {
        return this.$store.state.is_processing
      }
    }
  }
</script>



