<template>
  <modal :modal-class="'login'" :on-close="close">
    <h3 slot="header">{{headerTitle}}</h3>
    <form @submit.prevent="submitAction" id="form-login" name="form-login" class="forms">
      <div v-if="!isLoggedIn">
        <ul v-if="errors.errors.length" class="form-errors">
          <li v-for="error in errors.errors">{{ error.msg }}</li>
        </ul>
        <label>Login navn</label>
        <input type="text" name="login" v-model="login" v-validate.initial="login" data-rules="required|min:3" :class="{'has-error': errors.has('login') }" required>

        <label>Passord</label>
        <input v-model="password" v-validate data-rules="required|min:6" :class="{'input': true, 'has-error': errors.has('password') }" type="password" name="password" required>

      </div>
      <button type="submit" v-bind:disabled="errors.errors.length > 0" class="button-primary">{{buttonTitle}}</button>
    </form>

  </modal>
</template>


<style>
  @import "variables.css";

  .login .modal-container {
    width: 30rem;
    min-height: 23rem;
  }

  .login .modal--header {
    background: #16407d;
  }

</style>


<script>
  import Modal from './Modal'
  import router from '../router/index'

  export default {
    data: function() {
      return {
        login: '',
        password: ''
      }
    },
    components: {
      Modal
    },
    computed: {
      isLoggedIn () {
        return !!this.$store.state.user
      },
      headerTitle () {
        return this.isLoggedIn ? 'Logout' : 'Login'
      },
      buttonTitle () {
        return this.isLoggedIn ? 'Log out' : 'Log in'
      }
    },
    methods: {
      close: function(){
        router.push({name:'date', params: this.$route.params})
      },
      submitAction: function(e) {
        this.$validator.validateAll();

        if (this.errors.any()) {
          e.preventDefault();
          return;
        }

        if(this.isLoggedIn){
          this.$store.dispatch('logoutUser');
          return;
        }

        //if everything is OK
        this.$store.dispatch('loginUserAndReturnPromise', {
          login: this.login,
          password: this.password
        }).catch((e)=>{
          // TODO: check error message from server
          this.errors.add('password', e.message || 'Undefined error on server.');
          this.password = '';
        });

      }
    }
  }
</script>



