import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from 'vuex-persistedstate'
import db from '../helpers/Db'
import {reduce, reject, debounce, clone} from 'lodash'

Vue.use(Vuex)

function setLoaderOn(commit) {
  "use strict";
  commit('SET_PROCESSING_ON');
}

const setLoaderOff = debounce((commit) => { commit('SET_PROCESSING_OFF'); }, 500);

const state = {
  is_processing: false,
  /* day, week, month */
  display_mode_compact: false,
  timeline_mode: 'day', // day, week, month, year
  events_filter: {
    organization_id: false,
    level_id: false,
    region_id: false,
    category_id: false
  },
  events: [],
  event: {},
  categories: [],
  regions: [],
  levels: [],
  organizations: [],
  user: {}
}

const getters = {
  categoriesTree: (state) => {
    return reduce(state.categories, function (result, value) {
      if (value.parent_id) {
        (result[value.parent_id] || (result[value.parent_id] = {options: []})).options.push(value);
      } else {
        result[value.objectId] = Object.assign({}, result[value.objectId] || {options: []}, value)
      }
      return result;
    }, {})
  }
}

const mutations = {
  SET_PROCESSING_ON(state) {
    state.is_processing = true
  },
  SET_PROCESSING_OFF(state) {
    state.is_processing = false
  },
  SWITCH_MODE (state) {
    state.display_mode_compact = !state.display_mode_compact;
  },
  SWITCH_TIMELINE_MODE (state, data) {
    state.timeline_mode = data;
  },
  LOAD_CATEGORIES (state, data) {
    state.categories = data;
  },
  LOAD_REGIONS (state, data) {
    state.regions = data;
  },
  LOAD_ORGANIZATONS (state, data) {
    state.organizations = data;
  },
  LOAD_LEVELS (state, data) {
    state.levels = data;
  },
  LOAD_EVENTS (state, data) {
    state.events = data;
  },
  LOAD_EVENT (state, data) {
    state.event = data;
  },
  UNLOAD_EVENT (state) {
    state.event = {};
  },
  SAVE_EVENT (state, data) {
    state.events = reject(state.events, { objectId: data.objectId });
    state.events.push(data);
  },
  REMOVE_EVENT_BY_ID (state, data) {
    state.events = reject(state.events, { objectId: data });
  },
  FILTER_BY_ORGANIZATION (state, data) {
    state.events_filter.organization_id = data || false;
  },
  FILTER_BY_LEVELS (state, data) {
    state.events_filter.level_id = data || false;
  },
  FILTER_BY_REGIONS (state, data) {
    state.events_filter.region_id = data || false;
  },
  FILTER_BY_CATEGORY (state, data) {
    state.events_filter.category_id = data || false;
  },
  SET_USER (state, data) {
    state.user = data;
  }
}


const actions = {
  setProcessingOn({commit}) {
    commit('SET_PROCESSING_ON');
  },
  setProcessingOff({commit}) {
    commit('SET_PROCESSING_OFF');
  },
  loadCategories({commit}) {
    db.Categories.getAll().then(function (data) {
      commit('LOAD_CATEGORIES', data)
    });
  },
  loadOrganizations({commit}) {
    db.Organizations.getAll().then(function (data) {
      commit('LOAD_ORGANIZATONS', data)
    });
  },
  loadRegions({commit}) {
    db.Regions.getAll().then(function (data) {
      commit('LOAD_REGIONS', data)
    });
  },
  loadLevels({commit}) {
    db.Levels.getAll().then(function (data) {
      commit('LOAD_LEVELS', data)
    });
  },
  loadEvents({commit}) {
    db.Events.getAll().then(function (data) {
      commit('LOAD_EVENTS', data)
    })
  },
  loadEvent({commit}, data) {
    db.Events[data ? 'getById' : 'getNewEvent'](data)
      .then((event) => {
        commit('LOAD_EVENT', event)
      });
  },
  unloadEvent({commit}) {
    commit('UNLOAD_EVENT')
  },
  saveEvent({commit}, data) {
    setLoaderOn(commit);
    db.Events.saveEvent(data)
      .then((event) => {
        commit('SAVE_EVENT', event);
        setLoaderOff(commit);
      })
      .catch(() => {
        setLoaderOff(commit);
      });
  },
  removeEventById({commit}, data) {
    setLoaderOn(commit);
    db.Events.removeEventById(data)
      .then(() => {
        commit('REMOVE_EVENT_BY_ID', data);
        setLoaderOff(commit);
      })
      .catch(() => {
        setLoaderOff(commit);
      });
  },
  saveSubscription({commit}, data) {
    setLoaderOn(commit);
    return db.Subscriptions.saveSubscription(data)
      .then((event) => {
        setLoaderOff(commit);
        return event;
      })
      .catch((e) => {
        setLoaderOff(commit);
        return e;
      });
  },
  switchMode ({commit}) {
    commit('SWITCH_MODE')
  },
  switchTimelineMode ({commit}, mode) {
    commit('SWITCH_TIMELINE_MODE', mode)
  },
  switchFilterByOrganization ({commit}, mode) {
    commit('FILTER_BY_ORGANIZATION', mode)
  },
  switchFilterByLevel ({commit}, mode) {
    commit('FILTER_BY_LEVELS', mode)
  },
  switchFilterByRegion ({commit}, mode) {
    commit('FILTER_BY_REGIONS', mode)
  },
  switchFilterByCategory ({commit}, mode) {
    commit('FILTER_BY_CATEGORY', mode)
  },
  loginUserAndReturnPromise ({commit}, data) {
    return db.User.login(data.login, data.password)
      .then((user) => {
        commit('SET_USER', user)
      })
    // return Promise.resolve('!');
    // return Promise.reject('!');
  },
  logoutUser ({commit}) {
    db.User.logout().then(() => {
      commit('SET_USER', false);
    });
  },
  getCurrentUser ({commit}) {
    db.User.getCurrentUser()
      .then(function (user) {
        commit('SET_USER', user)
      })
      .catch(() => {
        commit('SET_USER', false);
      });
  }
}

const store = new Vuex.Store({
  state,
  getters,
  mutations,
  actions,
  strict: true, //!(process.env.NODE_ENV === 'development'),
  plugins: [createPersistedState()]
})

export default store



