<template>
  <input id="autocomplete" type="text" v-bind:value="markerAddress">
</template>

<script>
  import GoogleMapsLoader from '../helpers/GoogleMaps'

  export default {
    props: ['latitude', 'longitude', 'callback'],
    data: function () {
      return {
        markerAddress: ''
      }
    },
    mounted() {
      GoogleMapsLoader.load((google) => {
        var autocomplete = new google.maps.places.Autocomplete(document.getElementById('autocomplete'));
        autocomplete.addListener('place_changed', () => {
          var place = autocomplete.getPlace();
          if (!place.geometry) {
            return;
          }

          if (typeof this.callback === 'function') {
            this.callback(place.geometry.location.lat(), place.geometry.location.lng())
          }
        });

        this.autoComplete();
      });
    },
    methods: {
      autoComplete() {
        GoogleMapsLoader.load((google) => {
          if (!this.latitude || !this.longitude) {
            this.markerAddress = '';
            return;
          }

          var getJSON = function (url, callback) {
            var request = new XMLHttpRequest();
            request.open("get", url, true);
            request.responseType = "json";
            request.onload = function () {
              var status = request.status;
              if (status == 200) {
                callback(null, request.response);
              } else {
                callback(status);
              }
            };
            request.send();
          };
          var url = '//maps.googleapis.com/maps/api/geocode/json?latlng=' + this.latitude + ',' + this.longitude + '&sensor=true&language=no';
          getJSON(url, (err, data) => {
            this.markerAddress = (data.status == 'OK') ? data.results[0].formatted_address : '';
          });
        });
      }
    },
    watch: {
      latitude (data) {
        this.autoComplete()
      },
      longitude (data) {
        this.autoComplete()
      }
    }
  }


</script>



