<template>
  <div>
    <h2 v-translate>Time, Location, Map</h2>
    <p v-if="event.start_date">
      <span v-translate>Startdato</span>: {{newDateFormat(event.start_date)}}
    </p>
    <p v-if="event.end_date">
      <span v-translate>Startdato</span>: {{newDateFormat(event.end_date)}}
    </p>

    <event-view-map v-if="event.geo_latitude && event.geo_longitude" v-bind:event="event"></event-view-map>
    <event-view-autocomplete v-if="event.geo_latitude && event.geo_longitude" v-bind:event="event"></event-view-autocomplete>
    <event-view-description v-bind:event="event"></event-view-description>

  </div>
</template>

<script>
  import { mapState } from 'vuex';
  import fecha from 'fecha';
  import EventViewMap from './EventViewMap';
  import EventViewAutocomplete from './EventViewAutocomplete';
  import EventViewDescription from './EventViewDescription';

  export default {
    props: ['event'],
    components: {
      EventViewMap,
      EventViewAutocomplete,
      EventViewDescription
    },
    methods: {
      newDateFormat: function(data) {
        return fecha.format(data, 'DD.MM.YYYY');
      }
    }

  }
</script>





