import Backendless from '../Backendless';
import _ from 'lodash';

function Organizations(args) {
  args = args || {};

  this.updated = args.updated || null;
  this.logo = args.logo || null;
  this.small_logo = args.small_logo || null;
  this.name = args.name || null;
  this.ownerId = args.ownerId || null;
  this.objectId = args.objectId || null;
  this.created = args.created || null;
  this.description = args.description || null;

  this.___class = "Organizations";
}

function OrganizationClass() {
  let loadedOrganizations;

  const query = new Backendless.DataQuery();
  query.options = {
    pageSize: 100
  };

  const organizationCollection = Backendless.Persistence.of(Organizations);

  function getOrganizations() {
    if (!loadedOrganizations) {
      loadedOrganizations = loadOrganizations();
      return loadedOrganizations;
    }

    return getCachedOrganizations();
  }

  function loadOrganizations() {
    return organizationCollection.find(query)
      .then((response) => response.data)
      .then(function (data) {
        setCachedOrganizations(data);
        return data;
      })
      .catch((e) => {
        console.error(e);
        Promise.reject(e);
      });
  }

  function setCachedOrganizations(data) {
    loadedOrganizations = data;
  }

  function getCachedOrganizations() {
    return Promise.resolve(loadedOrganizations);
  }

  this.getAll = getOrganizations;

  this.getById = function (id) {
    return getOrganizations().then(function (data) {
      return _.find(data, {objectId: _.parseInt(id)});
    });
  };
}

export default OrganizationClass



