<template>
  <button @click.prevent="onClick" v-bind:class="[ klass ]"><slot></slot></button>
</template>

<script>
  import router from '../router'

  export default {
    props: ['klass', 'id'],
    methods: {
      onClick () {
        if(!window.confirm(this.$translate.locale['confirmEventDelete'])){
          return;
        }
        this.$store.dispatch('removeEventById', this.id);
        router.push({name:'date', params: this.$route.params});
      }
    }
  }
</script>



